/* universal  */
* {
  margin: 0;
  padding: 0%;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
}
.btn-primary {
  font-weight: bold;
}
/* toggle css dark mode light mood  */
body {
  background-color: white;
}

.darkModeBox {
  width: 83px;
  height: 35px;
  background-color: white;
  box-shadow: 0 0 15px 0 black;
  border-radius: 37px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}
.darkModeBtn {
  width: 33px;
  height: 95%;
  border-radius: 100%;
  background-color: white;
  margin-block: 4px;
  margin-left: 1px;
  border: 1px solid black;
  cursor: pointer;
  transition: left 0.3s ease;
  left: 0px;
  position: absolute;
}
.fa-moon {
  font-size: 30px;
  color: black;
  margin-left: 5px;
}
.fa-sun {
  font-size: 30px;
  color: rgb(255, 196, 3);
  margin-right: 5px;
}
/* navbar  component  */
.nav-section {
  background-color: cornflowerblue;
  /* position: fixed;
  top: 0%; */
  width: 100%;
}
nav {
  position: relative;
}
.navbar-brand {
  color: white !important;
  font-weight: bolder;
  font-size: 28px;
}
.nav-link {
  color: white !important;
  font-weight: 600;
  font-size: 22px;
  text-transform: capitalize;
}

/* text input component  */
label {
  font-size: 22px;
  font-weight: bold;
}
input,
select,
textarea {
  border: 1px solid black !important;
}
